@tailwind base;
@tailwind components;
@tailwind utilities;

/* Custom styles for TheSalesSherpa */

@layer base {
  body {
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
    @apply bg-gray-50;
  }
}

@layer components {
  /* Custom button variants */
  .btn-primary {
    @apply inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500;
  }
  
  .btn-secondary {
    @apply inline-flex items-center px-4 py-2 border border-gray-300 text-sm font-medium rounded-md shadow-sm text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500;
  }
  
  .btn-danger {
    @apply inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-red-600 hover:bg-red-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500;
  }

  /* Card styles */
  .card {
    @apply bg-white shadow rounded-lg border border-gray-200;
  }
  
  .card-header {
    @apply px-6 py-4 border-b border-gray-200;
  }
  
  .card-body {
    @apply px-6 py-4;
  }

  /* Status indicators */
  .status-hot {
    @apply text-red-600 bg-red-50 border-red-200;
  }
  
  .status-warm {
    @apply text-orange-600 bg-orange-50 border-orange-200;
  }
  
  .status-developing {
    @apply text-yellow-600 bg-yellow-50 border-yellow-200;
  }
  
  .status-nurture {
    @apply text-green-600 bg-green-50 border-green-200;
  }

  /* Separation degree indicators */
  .separation-1 {
    @apply bg-green-100 text-green-800;
  }
  
  .separation-2 {
    @apply bg-yellow-100 text-yellow-800;
  }
  
  .separation-3-4 {
    @apply bg-blue-100 text-blue-800;
  }
  
  .separation-5plus {
    @apply bg-gray-100 text-gray-800;
  }

  /* Custom loading spinner */
  .loading-spinner {
    @apply animate-spin rounded-full border-b-2 border-blue-600;
  }

  /* Gradient backgrounds */
  .gradient-primary {
    background: linear-gradient(135deg, #3B82F6 0%, #8B5CF6 100%);
  }
  
  .gradient-secondary {
    background: linear-gradient(135deg, #10B981 0%, #3B82F6 100%);
  }

  /* Custom shadows */
  .shadow-card {
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  }
  
  .shadow-card-hover {
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  }

  /* Typography */
  .text-heading {
    @apply text-2xl font-bold text-gray-900;
  }
  
  .text-subheading {
    @apply text-lg font-medium text-gray-900;
  }
  
  .text-body {
    @apply text-sm text-gray-600;
  }
  
  .text-caption {
    @apply text-xs text-gray-500;
  }

  /* Form elements */
  .form-input {
    @apply block w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm placeholder-gray-400 focus:outline-none focus:ring-blue-500 focus:border-blue-500 sm:text-sm;
  }
  
  .form-select {
    @apply block w-full pl-3 pr-10 py-2 text-base border-gray-300 focus:outline-none focus:ring-blue-500 focus:border-blue-500 rounded-md;
  }

  /* Navigation */
  .nav-item {
    @apply group flex gap-x-3 rounded-md p-2 text-sm leading-6 font-semibold;
  }
  
  .nav-item-active {
    @apply bg-gray-50 text-blue-600;
  }
  
  .nav-item-inactive {
    @apply text-gray-700 hover:text-blue-600 hover:bg-gray-50;
  }

  /* Alerts and notifications */
  .alert-success {
    @apply bg-green-50 border border-green-200 rounded-md p-4;
  }
  
  .alert-warning {
    @apply bg-yellow-50 border border-yellow-200 rounded-md p-4;
  }
  
  .alert-error {
    @apply bg-red-50 border border-red-200 rounded-md p-4;
  }
  
  .alert-info {
    @apply bg-blue-50 border border-blue-200 rounded-md p-4;
  }

  /* Data visualization */
  .progress-bar {
    @apply w-full bg-gray-200 rounded-full h-2;
  }
  
  .progress-fill {
    @apply h-2 rounded-full transition-all duration-300 ease-in-out;
  }

  /* Responsive grid layouts */
  .grid-accounts {
    @apply grid grid-cols-1 gap-6;
  }
  
  .grid-dashboard {
    @apply grid grid-cols-1 gap-5 sm:grid-cols-2 lg:grid-cols-4;
  }
  
  .grid-details {
    @apply grid grid-cols-1 lg:grid-cols-2 gap-6;
  }

  /* Animation utilities */
  .fade-in {
    animation: fadeIn 0.3s ease-in-out;
  }
  
  .slide-up {
    animation: slideUp 0.3s ease-out;
  }
  
  .pulse-soft {
    animation: pulseSoft 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
  }
}

@layer utilities {
  /* Custom animations */
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  
  @keyframes slideUp {
    from {
      opacity: 0;
      transform: translateY(10px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  @keyframes pulseSoft {
    0%, 100% {
      opacity: 1;
    }
    50% {
      opacity: 0.8;
    }
  }

  /* Utility classes for TheSalesSherpa branding */
  .text-sherpa-blue {
    color: #3B82F6;
  }
  
  .text-sherpa-purple {
    color: #8B5CF6;
  }
  
  .bg-sherpa-blue {
    background-color: #3B82F6;
  }
  
  .bg-sherpa-purple {
    background-color: #8B5CF6;
  }
  
  .border-sherpa-blue {
    border-color: #3B82F6;
  }

  /* Professional spacing */
  .section-padding {
    @apply py-12 px-6;
  }
  
  .card-padding {
    @apply p-6;
  }
  
  .content-padding {
    @apply px-4 sm:px-6 lg:px-8;
  }

  /* Typography scale */
  .text-display {
    @apply text-4xl font-bold tracking-tight text-gray-900;
  }
  
  .text-title {
    @apply text-3xl font-bold text-gray-900;
  }

  /* Interactive states */
  .interactive-card {
    @apply transition-all duration-200 ease-in-out hover:shadow-lg hover:-translate-y-1 cursor-pointer;
  }
  
  .interactive-button {
    @apply transition-all duration-150 ease-in-out hover:shadow-md active:scale-95;
  }

  /* Status colors for various UI elements */
  .status-online {
    @apply bg-green-500;
  }
  
  .status-offline {
    @apply bg-gray-400;
  }
  
  .status-syncing {
    @apply bg-blue-500;
  }
  
  .status-error {
    @apply bg-red-500;
  }
}

/* Global styles */
html {
  scroll-behavior: smooth;
}

/* Custom scrollbar styles */
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

::-webkit-scrollbar-track {
  background: #f1f5f9;
}

::-webkit-scrollbar-thumb {
  background: #cbd5e1;
  border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
  background: #94a3b8;
}

/* Focus styles for accessibility */
.focus-ring {
  @apply focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500;
}

/* Print styles */
@media print {
  .no-print {
    display: none !important;
  }
  
  .print-break {
    page-break-after: always;
  }
}